.cards-container
{
    .cards{
    @include cards-grid;

    .card{
        @include card;
        h3{
            color: rgb(3, 154, 255);
            font-weight: 500;
            margin-top: .5rem;
            font-size: 1.1rem;
            cursor: pointer;
            display: inline-block;
            transition: padding .25s;
            &:hover{
                color:rgb(14, 113, 179);
                border-left: 3px solid rgb(14, 113, 179);
                padding-left: .3rem;
            }
        }
        h4{
            font-weight: 500;
        }
        img{
            width: 100%;
            border-radius: .5rem;
        }
    }

}
.pagination-section{
    @include flex-center;
    margin: 2rem 0;
}
}